.TodoSearchLabel{
  display: block;
  margin: 0 auto;
  margin-bottom: 35px;
  text-align: center;
}
.TodoSearchInput{
  text-align: center;
  min-height: 34px;
  width: 70%;
  border: 1px solid #091354;
  color: white;
  background-color: #091354;
}
.TodoSearchInput:focus{
  outline: 1px #d304fe;
}
.TodoSearchInput:hover{
  border: 2px solid #d304fe;
}
