.icon-complete svg{
  display: inline-block;
  width: 32px;
  height: 32px;
}

.icon-delete {
  position: absolute;
  top: calc(0% - 10px);
  left: calc(100% - 12px);
  width: 20px;
  height: 20px;
}
.icon-complete svg:hover{
  fill:#d021fc;
}
.icon-delete svg:hover{
  fill: red;
}
